.toasts{position:fixed;top:2rem;right:2rem;z-index:10}.toast{position:relative;max-width:40rem;-webkit-animation:toastAppear 500ms cubic-bezier(0.55, 0, 0.1, 1) forwards;animation:toastAppear 500ms cubic-bezier(0.55, 0, 0.1, 1) forwards}.toast__close{position:absolute;top:1.3rem;right:2rem;font-weight:400;font-size:2.2rem;cursor:pointer}@-webkit-keyframes toastAppear{0%{opacity:0;-webkit-transform:translateY(-1rem);transform:translateY(-1rem)}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes toastAppear{0%{opacity:0;-webkit-transform:translateY(-1rem);transform:translateY(-1rem)}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}



